.section {
  display: flex;
  flex-direction: column-reverse;
  justify-content: center;
  align-items: center;
  text-align: center;
  max-width: 1220px;
  width: calc(100% - 2rem);
  z-index: -2;
  border-radius: 6px;
  margin: 0 auto 6em;

  @media (min-width: 680px) {
    margin-bottom: 12em;
  }

  @media (min-width: 1180px) {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    text-align: left;
  }

  .col1 {
    h2,
    p {
      color: var(--clr-primary-light);
      max-width: 580px;
    }
  }

  .col2 {
    position: relative;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(4, 1fr);
    max-width: 532px;
    aspect-ratio: 1;
    background-image: url("../assets/square-bg-grid.png");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    margin-bottom: 2em;

    @media (min-width: 540px) {
      grid-template-columns: 2fr 1fr 3fr 0.5fr;
      grid-template-rows: 0.5fr 2fr 1fr 1fr 1fr;
      margin-right: -1em;
    }

    @media (min-width: 980px) {
      margin-bottom: 0;
    }

    @media (min-width: 1180px) {
      transform: scale(1);
    }

    :nth-child(-n + 2) {
      object-fit: cover;
    }

    img {
      border-radius: 6px;
      width: 100%;
      height: 100%;
      opacity: 1;

      &:nth-of-type(1) {
        grid-column: 2 / 5;
        grid-row: 1 / -2;
        @media (min-width: 540px) {
          grid-column: 2 / 4;
          grid-row: 1 / 5;
        }
      }

      &:nth-of-type(2) {
        grid-column: 1/ 3;
        grid-row: 3/ 5;
        @media (min-width: 540px) {
          grid-column: 1/ 3;
          grid-row: 3/ 6;
        }
      }

      &:nth-of-type(3) {
        width: 80px;
        aspect-ratio: 1/2;
        grid-column: 1 / 2;
        grid-row: 1 / 3;
        opacity: 0;
        @media (min-width: 540px) {
          transform: translateX(30%);
          grid-column: 1 / 2;
          grid-row: 2 / 3;
        }
      }

      &:nth-of-type(4) {
        width: 80px;
        aspect-ratio: 1/2;
        transform: rotate(200deg);
        grid-column: 4 / 5;
        grid-row: 4 / 5;
        opacity: 0;

        @media (min-width: 540px) {
          transform: translateX(200%) rotate(200deg);
          grid-column: 2 / 3;
          grid-row: 5 / 6;
        }
      }
    }
  }
}
